@charset "utf-8";

*{
box-sizing: border-box;
-webkit-box-sizing: border-box;
-moz-box-sizing: border-box;
padding: 0px;
margin: 0px;
}

html{
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(32, 32, 32);
  height: 100%;
  width: 100%;
}

#root{
  height: 90vh;
  width: 100%;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.textInfo{
  color: white;
  font-weight: bold;
  position: relative;
  font-size: 4vh;
  margin-top: 1.5vh;
}

.Morpion{
  width: calc(100% /3);
  height: calc(100% /3);
  display: inline-block;
  vertical-align: top;
  border: 2px solid black;
}

.case{
  user-select: none;
  width: calc(100% /3);
  height: calc(100% /3);
  display: inline-block;
  vertical-align: top;
  border: 1px solid black;
  font-size: calc(80vh/13);
  font-weight: bold;
}

.Board {
  position: relative;
  height: 80vh;
  width: 80vh;
  background-color: rgb(228, 228, 228);
  margin: auto;
  margin-top: 44.5vh;
  transform: translateY(-50%);
}

.morpionGagne{
  width: calc(100% /3);
  height: calc(100% /3);
  display: inline-block;
  vertical-align: top;
  border: 2px solid black;
  user-select: none;
  font-size: calc(80vh/4.5);
  font-weight: bold;
}

/* Add a black background color to the top navigation */
.topnav {
  background-color: #333;
  overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Add a color to the active/current link */
.topnav a.active {
  background-color: #04AA6D;
  color: white;
}

.playable {
  background-color: rgba(138, 228, 160, 0.884);
}

.lastPlayed{
  background-color: rgba(230, 216, 99, 0.904);
}

.Win{
  background-color: rgb(32, 32, 32);
  box-shadow:5px 5px 5px rgb(48, 48, 48),
-5px -5px 5px  rgb(48, 48, 48);
  border-radius: 5%;
  position: absolute;
  top:24%;
  left: 10%;
  margin: auto;
  width: 80%;
  display: block;
  color: white;
  font-size: 4vh;
  font-weight: bold;
}

.Win img{
  display: block;
  margin: auto;
  width: 80%;
}



.Win button {
  color: white;
  background-color: #008d23;
  padding: 3px 20px;
  border: 0px;
  margin-bottom: 2%;
  border-radius: 100px;
  font-weight: bold;
  font-size: 3vh;
}

button:hover{
  transition-duration: 0.4s;
  transition-property: background-color;
  background-color: #00691a;
}

.fa-spin{
  position: relative;
  left: 8%;
}

.container{
  position: relative;
  right: -10vw;
  height: 90vh;
  display: inline-block;
}

.menu{
  position: relative;
  right: -10vw;
  height: 80vh;
  width: 20vw;
  display: inline-block;
  vertical-align: top;
  text-align: left;
  margin-top: 11.3vh;
}

.myButton {
  margin-left: 30px;
  margin-bottom: 10px;
	background-color:#008d23;
	border-radius:10px;
  text-align: center;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-size: 25px;
	font-weight:bold;
  padding: 5px 20px;
	text-decoration:none;
}

.myButton:hover {
	background-color:#00691a;
}

.myButton:active {
	position:relative;
	top:1px;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
